import $ from "jquery";
import React from 'react';
import { BaseController }  from   "./common/BaseController.js"
import { NavBar }  from   "../components/NavBar.js"
import {  Container}  from   "../components/Container.js"
import banner from '../prototype/images/banner.png';
import bot1_on from '../prototype/images/bot1_on.png';
import bot2 from '../prototype/images/bot2.png';
import bot3 from '../prototype/images/bot3.png';
import { HomePage } from './HomePage.js';
import { Order } from './Order.js'
import { Mine } from './Mine.js'


var ReactDOM = require("react-dom");
export class Main extends BaseController {
    /*
     * 功能 : 组件渲染完成通知函数
     * 注意 : 一定要调用super.componentDidMount();  或者把这个通知去掉
     */
    componentDidMount() {
        super.componentDidMount();
    }
    /*
     * 功能 :  组件恢复显示通知函数
     * 注意 :  A调到B  B在返回的时候 A会收到此通知
     */
    componentResume () {
        super.componentResume();
    }
    /*
     * 功能 :  组件暂停显示通知函数
     * 注意 :  A调到B   A会收到此通知
     */
    componentPause () {
        super.componentPause();
    }
    /*
     * 功能 :  组件被卸载通知函数
     */
    componentWillUnmount () {
        super.componentWillUnmount();
    }
    //首页按钮点击事件
    onHomeBtnTap(){
        console.log('首页');
    }
    //一键下单点击事件
    onOrderBtnTap(){
        console.log('一键下单');
    }
    //我的点击事件
    onMineBtnTap(){
        console.log('我的');
    }
    render() {
        return (
                <div>
                    <NavBar title="首页"
                        leftIcon={null}
                        bgColor="#00b48b">
                    </NavBar>
                    <div>
                        <div>
                            <HomePage></HomePage>
                        </div>


                        <ul className="foot clearfix">
                            <li onTouchTap={this.onHomeBtnTap.bind(this)}>
                                <img src={bot1_on} width="22" height="22"></img>
                                <div className="bot_tt col-54e">首页</div>
                            </li>
                            <li onTouchTap={this.onOrderBtnTap.bind(this)}>
                                <img src={bot2} width="22" height="22"></img>
                                <div className="bot_tt">一键下单</div>
                            </li>
                            <li onTouchTap={this.onMineBtnTap.bind(this)}>
                                <img src={bot3} width="22" height="22"></img>
                                <div className="bot_tt">我的</div>
                            </li>
                        </ul>
                    </div>
                </div>


        )
    }
}







