//首页
import $ from "jquery";
import React from 'react';
import { BaseController }  from   "./common/BaseController.js"
import { NavBar }  from   "../components/NavBar.js"
import {  Container}  from   "../components/Container.js"
import banner from '../prototype/images/banner.png';
import bot1_on from '../prototype/images/bot1_on.png';
import bot2 from '../prototype/images/bot2.png';
import bot3 from '../prototype/images/bot3.png';


var ReactDOM = require("react-dom");
export class HomePage extends React.Component {


    //跳转至电子病例页面
    onCaseTap(){
        console.log('电子病例');
    }
    //跳转至基因报告页面
    onRecordTap(){
        console.log('基因报告');
    }
    //跳转至辅助诊断页面
    onDiagnoseTap(){
        console.log('辅助诊断');
    }
    //跳转至案例分享页面
    onShareTap(){
        console.log('案例分享');
    }
    //跳转至常见OA页面
    onOATap(){
        console.log('常见OA');
    }
    //跳转至在线交流页面
    onCommunicationTap(){
        console.log('在线交流');
    }

    render() {
        return (
            <div>
                <img src={banner} width="100%" ></img>

                <ul className="menu_list clearfix" >
                    <li onTouchTap={this.onCaseTap.bind()}>
                        <div className="menu_left">
                            <div className="menu_tt" >电子病历</div>
                            <div  >往期病历浏览</div>
                        </div>
                        <div className="menu1"></div>
                        <div className="clear"></div>

                    </li>
                    <li onTouchTap={this.onRecordTap.bind(this)}>
                        <div className="menu_left">
                            <div className="menu_tt col-54e" >基因报告</div>
                            <div className="menu_info">往期病历浏览</div>
                        </div>
                        <div className="menu2"></div>
                        <div className="clear"></div>
                    </li>

                </ul>
                <ul className="menu_list2 clearfix">
                    <li onTouchTap={this.onDiagnoseTap.bind(this)}>
                        <div className="menu_left">
                            <div className="menu_tt col-fe8" >辅助诊断</div>
                            <div className="menu_info">辅助查询病历</div>
                        </div>
                        <div className="menu3"></div>
                        <div className="clear"></div>
                    </li>
                    <li onTouchTap={this.onShareTap.bind(this)}>
                        <div className="menu_left">
                            <div className="menu_tt col-16c" >案例分享</div>
                            <div className="menu_info">权威分析病情</div>
                        </div>
                        <div className="menu4"></div>
                        <div className="clear"></div>
                    </li>
                </ul>
                <ul className="menu_list1 clearfix">
                    <li onTouchTap={this.onOATap.bind(this)}>
                        <div className="menu_left">
                            <div className="menu_tt col-fe8" >常见OA</div>
                            <div className="menu_info">辅助查询病历</div>
                        </div>
                        <div className="menu3"></div>
                        <div className="clear"></div>
                    </li>
                    <li onTouchTap={this.onCommunicationTap.bind(this)}>
                        <div className="menu_left">
                            <div className="menu_tt col-54e" >在线交流</div>
                            <div className="menu_info">权威分析病情</div>
                        </div>
                        <div className="menu4"></div>
                        <div className="clear"></div>
                    </li>

                </ul>
            </div>
        )
    }
}



